import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Avatar, Grid, Paper,  Container, GridDirection, PropTypes } from "@material-ui/core";
import {useStyles} from "../Styles";
import {componentProps} from "../ComponentInterface";
import { TypeBackground } from '@material-ui/core/styles/createPalette';
import {theme} from "../../theme/theme";
/* tslint:disable */
const imgLink =
  "https://picsum.photos/200/300";



const CardBoard1 = (props : any) =>{
  const styles = useStyles();
  function VarDark (prop:any) {
      return (
          <Grid item xs={6} >
              <Card  color={theme.palette.primary.dark} >
                  <CardActionArea>

                      <CardContent style={{backgroundColor: theme.palette.primary.dark}}>
                          <Typography gutterBottom variant="h4" component="h2">
                          {prop.title}
                          </Typography>
                          <Typography variant="body2" color="textSecondary" component="p">
                             {prop.content}
                          </Typography>
                      </CardContent>
                  </CardActionArea>
              </Card>
          </Grid>
      )
  }

  function VarLight (prop:any) {
      return(
          <Grid item xs={6} >
              <Card >
                  <CardActionArea>
                      <CardContent style={{backgroundColor: theme.palette.primary.light}}>
                          <Typography gutterBottom variant="h4" component="h2">
                            {prop.title}
                          </Typography>
                          <Typography variant="body2" color="textSecondary" component="p">
                            {prop.content}
                          </Typography>
                      </CardContent>
                  </CardActionArea>
              </Card>
          </Grid>
      )
  }



    return (

    <Container>
        <Grid
        container
        alignItems="center"
        justifyContent="center"
        style={{marginTop:"8%"}}
        >

            <Paper>
                {props.children}
            </Paper>

            <Grid container item xs={12} spacing={0} justifyContent="center" >
                <VarLight  title={props.mainTitle1} content={props.mainContent1}/>
                <VarDark title={props.mainTitle2} content={props.mainContent2} />
                <VarDark title={props.mainTitle3} content={props.mainContent3}/>
                <VarLight title={props.mainTitle4} content={props.mainContent4}/>
                <VarLight  title={props.mainTitle5} content={props.mainContent5}/>
                <VarDark  title={props.mainTitle6} content={props.mainContent6}/>
                <VarDark title={props.mainTitle7} content={props.mainContent7}/>
                <VarLight title={props.mainTitle8} content={props.mainContent8}/>
            </Grid>
        </Grid>

   </Container>
  );
}

export default CardBoard1;