import React from "react";

import { Avatar, Grid, Paper, Typography, Container, GridDirection, PropTypes } from "@material-ui/core";
import {componentProps} from "../ComponentInterface";
import {useStyles} from "../Styles";
import { Direction } from "readline";

const imgLink =
  "https://picsum.photos/2000/300";

  const Comments2 = (props : any) => {
    const styles = useStyles();

    let arr = [1,2,3,4,5,6];

    const renderCards = () => {
      
      return(
         arr.map((item,index)=>{

           let changeRow:GridDirection = (index%2 === 0 )?  "row-reverse": "row"  
           let changeAlign:PropTypes.Alignment = (index%2 === 0 )?  'right': 'left' 
           return(
          
           
          <Grid container   spacing={1} direction={changeRow} justify="center" alignItems="center"   md={12} className={styles.contentTransparentBg} >

          <Grid item md={2}>
                <Avatar className={styles.avatarLarge} alt="Remy Sharp" src={imgLink} style={{width:"160px", height:"160px"}} />
          </Grid>
          
            <Grid item md={10} sm={12} xs={12} >
              <Grid item md={12} direction={changeRow}>
                  <Typography variant="h5" gutterBottom  align={changeAlign} >
  
                          {props.mainTitle}
  
                  </Typography>
                  </Grid>
                  <Typography variant="body1" paragraph>
  
                        {props.mainContent}
  
                  </Typography>
                  
            </Grid>
           
        </Grid>
        
        
         )})
         )
  }


    return (
      <Container >
        <Paper>
              {props.children}
        </Paper>
      <Typography variant="h2" gutterBottom  align='center'  >
        
          {props.mainTitle}

      </Typography>

      <Grid container   spacing={1} direction={"row" } justify="center" alignItems="center"   md={12} className={styles.contentTransparentBg} >

        <Grid item md={2}>
              <Avatar className={styles.avatarLarge} alt="Remy Sharp" src={props.img1} style={{width:"160px", height:"160px"}} />
        </Grid>

          <Grid item md={10} sm={12} xs={12} >
            <Grid item md={12} direction={"row" }>
                <Typography variant="h5" gutterBottom  align={'left'} >

                        {props.mainTitle1}

                </Typography>
                </Grid>
                <Typography variant="body1" paragraph>

                      {props.mainContent1}

                </Typography>
                
          </Grid>
        
        </Grid>
              <Grid container   spacing={1} direction={"row-reverse"} justify="center" alignItems="center"   md={12} className={styles.contentTransparentBg} >

        <Grid item md={2}>
              <Avatar className={styles.avatarLarge} alt="Remy Sharp" src={props.img2} style={{width:"160px", height:"160px"}} />
        </Grid>

          <Grid item md={10} sm={12} xs={12} >
            <Grid item md={12} direction={"row-reverse"}>
                <Typography variant="h5" gutterBottom  align={'right'} >

                        {props.mainTitle2}

                </Typography>
                </Grid>
                <Typography variant="body1" paragraph>

                      {props.mainContent2}

                </Typography>
                
          </Grid>
        
        </Grid>

        <Grid container   spacing={1} direction={"row" } justify="center" alignItems="center"   md={12} className={styles.contentTransparentBg} >

        <Grid item md={2}>
              <Avatar className={styles.avatarLarge} alt="Remy Sharp" src={props.img3} style={{width:"160px", height:"160px"}} />
        </Grid>

          <Grid item md={10} sm={12} xs={12} >
            <Grid item md={12} direction={"row" }>
                <Typography variant="h5" gutterBottom  align={'left'} >

                        {props.mainTitle3}

                </Typography>
                </Grid>
                <Typography variant="body1" paragraph>

                      {props.mainContent3}

                </Typography>
                
          </Grid>
        
        </Grid>
        
      </Container>
  );
}

export default Comments2;