import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Grid } from '@mui/material';

export default function AccordionMy(props) {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Grid container
        direction="column"
        justifyContent="center"
        alignItems="center" 
        style={{marginTop:"5%"}}
    >
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} style={{width:"35%"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography sx={{ width: '33%', flexShrink: 0 }}>
            {props.mainTitle1}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
                {props.mainContent1}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')} style={{width:"35%"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography sx={{ width: '33%', flexShrink: 0 }}>{props.mainTitle2}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          {props.mainContent2}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')} style={{width:"35%"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography sx={{ width: '33%', flexShrink: 0 }}>
          {props.mainTitle3}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          {props.mainContent3}
          </Typography>
        </AccordionDetails>
      </Accordion>
    </Grid>
  );
}
