import React from 'react';
import {Card, Container, Grid, Paper} from "@material-ui/core";
import {componentProps} from "../ComponentInterface";
import {useStyles} from "../Styles";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardMedia from "@material-ui/core/CardMedia";
import img1 from "../../static/images/img1.jpg";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import {theme} from "../../theme/theme";

const imgLink =
  "https://picsum.photos/500/300";

const CardNews1 = (props:any) => {
    const styles = useStyles();
    let arr = [1,2,3,4];


    return(
        <Container>
            <Paper>
            {props.children}
            </Paper>
                <Grid container className={styles.contentTransparentBg} >
                    
                    <Typography gutterBottom variant="h4" component="h2">
                        {props.mainTitle}
                    </Typography>
                    <Grid container item xs={12} spacing={1}   className={styles.contentTransparentBg} >
                    <Grid item  xs={6} >
                        <Card >
                        <CardMedia component="img"   max-height="60"  src={props.img1}  style={{height:300}}   />
                            <CardContent >
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {props.mainContent1}
                                </Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item  xs={6} >
                        <Card >
                        <CardMedia component="img"    min-height="60"  src={props.img2}  style={{height:300}}  />
                            <CardContent >
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {props.mainContent2}
                                </Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                    </Grid>
                </Grid>
        </Container>
    )
}

export default CardNews1;
